/* For demo */
.banner-user{
  height: 92vh;
  width: 100%!important;
}
.banner-user-elem{
  height: 100%;
  
}
.banner-elemento{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.521)!important;
  backdrop-filter: blur(2px);
}

.img-banner{
  max-width: 120%!important;
  max-height: 120%!important;
}
@media(max-width: 900px){
  .banner-user{
    height: 30vh!important;
  }
  .img-banner{
    max-width: 120%!important;
    max-height: 120%!important;
  }
}
.search-nav .ant-input-wrapper{
  border-radius: 150px;
}
